        <p style="color: white; display: inline-block">{{cartSize}}</p>
      </div>
      
      <div id="pages">
        <router-link to="/">Home</router-link>
        <router-link to="/order">Order</router-link>
      </div>
    </div>

    <transition name="fade" mode="out-in">
      <router-view v-on:cartInc="incCart" />
    </transition>
  </div>
</template>

<script>
export default {
  name : 'App',
  data() {
    return {
      cartSize : localStorage.getItem('cart') == null ? 0 : JSON.parse(localStorage.getItem('cart')).length
    }
  },
  created() {
    localStorage.setItem('inst', true);
  },
  methods : {
    toCart() {
      console.log('to cart!');

      this.$router.push('cart');
    },
    incCart() {
      this.cartSize = JSON.parse(localStorage.getItem('cart')).length;
    }
  }
}
</script>


